  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 1.16;
  display: flex;
  position: relative;
}

.header__drop-container {
  width: 144px;
  height: 114px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: #fff;
  border-radius: 0 0 72px 72px;
  flex-direction: column;
  justify-content: end;
  gap: 4px;
  padding-bottom: 12px;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: -12px;
  left: -50%;
  transform: translateY(-10%);
  box-shadow: 20px 20px 80px rgba(0, 0, 0, .1);
}

.header__drop-container:hover, .header__drop-container:focus, .header__nav-btn:focus + .header__drop-container {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

.header__drop-link {
  text-align: center;
  color: #2b2b2b;
  font-size: 16px;
  line-height: 1.18;
  display: block;
}

.header__drop-icon {
  width: 10px;
  height: 4px;
  margin-left: 4px;
}

.header__nav-btn:focus .header__drop-icon {
