.header__speech {
  z-index: 1;
  width: 162px;
  height: 82px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: #fff;
  border-radius: 2px;
  padding: 0 6px 0 8px;
  font-family: Cera Pro;
  font-size: 16px;
  line-height: 1.25;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50px;
  left: -44px;
  transform: translateY(-50%);
  box-shadow: 4px 4px 20px rgba(0, 0, 0, .1);
}

.header__speech:after {
  content: " ";
  width: 0;
  height: 0;
  border-bottom: 20px solid #fff;
  border-left: 22px solid rgba(0, 0, 0, 0);
  border-right: 22px solid rgba(0, 0, 0, 0);
  border-radius: 2px;
  position: absolute;
  top: 9px;
  left: 50%;
  transform: translate(-50%, -100%);
}

